import dayjs from 'dayjs';

// JSON    
export const groupPostsByDate = (posts) => {
    return posts.reduce((acc, post) => {
        const date = dayjs(post.createdAt).format('YYYY-MM-DD');  //  
        if (!acc[date]) acc[date] = [];
        acc[date].push(post);
        return acc;
    }, {});
};
