import { StyledCalendarWrapper, StyledCalendar } from "./StyledCalendar";
import moment from 'moment';

function DatePicker({ onDateChange, date, posts }) {
    const hasDataForDate = (date) => {
        const formattedDate = moment(date).format('YYYY-MM-DD');
        return !!posts[formattedDate]; //     
    };

    return (
        <StyledCalendarWrapper>
            <StyledCalendar
                value={new Date(date)}
                onChange={onDateChange}
                formatDay={(locale, date) => moment(date).format("D")} //    
                formatYear={(locale, date) => moment(date).format("YYYY")} //     
                formatMonthYear={(locale, date) => moment(date).format("YYYY. MM")} //  2023. 12   
                calendarType="gregory" //   
                showNeighboringMonth={false} // ,   
                next2Label={null} // +1 & +10   
                prev2Label={null} // -1 & -10   
                minDetail="year" // 10  
                tileClassName={({ date, view }) =>
                    view === 'month' && hasDataForDate(date) ? 'highlight' : null
                }
            />
        </StyledCalendarWrapper>
    )
}

export default DatePicker;